@font-face {
    font-family: fre;
    src: url(../../Srift/19413.ttf);
}


.frend{
    margin-top: 50px;
}
.frend img{
    width: 100px;
    height: 100px;
}
.data{
    margin-left: 30px;
    margin-bottom: 30px;
    display: grid;
    grid-template-columns:2fr 10fr 6fr;
}
.description{
    border:  5px #121617 solid;
    display: grid;
    grid-template-areas:
     'n con'
     's cit';
    font-family: fre, sans-serif;
    font-size: 150%;
    font-weight: 600;
    color: white;

}
.name{
    grid-area: n;
}
.status{
   grid-area: s;
    grid-row-start: 3;
}
.country{
    grid-area: con;
    justify-self: end;

}
.city{
    grid-area: cit;
    justify-self: end;
    grid-row-start: 3;
}
.ava button {
    width: 60%;
    height: 4vh;
    cursor: pointer;
    font-family: fre, sans-serif;
    font-size: 120%;
    font-weight: 550;
    border-radius: 10%;
}
.green {
    background: green;
    color: white;
}
.red {
    background: red;
}
.pages {
    font-size: 200%;
    text-align: center;
    cursor: pointer;



}
.page{
    font-size: 400%;
    width: 500px;
    border:#121617 2px solid ;

}
.paginat{
    margin-left: 1.5%;
}
.paginat button {
    width: 3.3%;
    height: 5vh;
    font-size: 100%;
   font-family: cvt, sans-serif;
}
.paginat .big {
    width: 5%;
    height: 5vh;
    font-size: 80%;
}
.pageSize {
    background: darkorange;
}