

.users a{
  font-size: 200%;
  margin: 10%;
  line-height: 250%;
  text-decoration: none;
  color: #121617;
}
 .users a.active {
  color: white;
}
a:hover{
  color: #31e508;
}
.users img {
    width: 80px;
    height: 70px;
    padding-left: 15px;
}
.us {
    display: grid;
    grid-template-columns: 1fr 5fr;
}
